import React from 'react'
import { Container, Row, Col, Form, Input, FormGroup, Label, Button } from 'reactstrap'
import ReactLoading from 'react-loading'


class LoginForm extends React.Component {
    state = {
        uid: '',
        username: '',
        password: '',
        isLoading: false
    }
    
    onSubmit(e) {
        this.setState({ isLoading: true })

        // this.setState({ isLoading: false })

        e.preventDefault()
    }

    render() {
        let { username, password, isLoading } = this.state
        
        return (
            <Container>
                <Row>
                    <Col>
                        <div className="p-2 bg-secondary text-center text-white">
                            Login
                        </div>
                    </Col>
                </Row>
                <Row>
                    <Col>
                        <div className="p-4 bg-white border font-weight-light">
                            <Form onSubmit={ e => e.preventDefault() }>
                                <div>
                                    <FormGroup>
                                        <Label>Username</Label>
                                        <Input type="text" />
                                    </FormGroup>
                                </div>
                                <div>
                                    <FormGroup>
                                        <Label>Password</Label>
                                        <Input type="password"/>
                                    </FormGroup>
                                </div>
                                <div>
                                    <Button type="submit" color="success" block>
                                        {
                                            isLoading && <ReactLoading type="spin" width={ 24 } height={ 24 } color="white"/>
                                        }
                                        {
                                            !isLoading && 'Login'
                                        }
                                    </Button>
                                </div>
                            </Form>
                        </div>
                    </Col>
                </Row>
            </Container>
        )
    }
}

export default LoginForm


